export function buildMenuTree(list) {
  let tree = [];
  list.forEach((item) => {
    let curObj = null;
    let itemArr = item.split("/");
    itemArr.shift();
    if (itemArr.length === 1) {
      tree.push({
        label: itemArr[0],
        key: getTreeKey(itemArr, 1),
      });
      return;
    }
    for (let i in itemArr) {
      if (i === "0") {
        curObj = tree.find((item) => item.label === itemArr[i]);
        if (!curObj) {
          curObj = {
            label: itemArr[i],
            key: getTreeKey(itemArr, i),
            children: [],
          };
          tree.push(curObj);
        }
      } else if (i === String(itemArr.length - 1)) {
        curObj.children.push({
          label: itemArr[i],
          key: getTreeKey(itemArr, i),
        });
      } else {
        let obj = curObj.children.find((item) => item.label === itemArr[i]);
        if (!obj) {
          obj = {
            label: itemArr[i],
            key: getTreeKey(itemArr, i),
            children: [],
          };
          curObj.children.push(obj);
          curObj = obj;
        } else {
          curObj = obj;
        }
      }
    }
  });
  console.log(tree)
  return tree;
}

function getTreeKey(arr, index) {
  return index + "-key-/" + arr.slice(0, index + 1).join("/");
}
