import { Menu } from "antd";

import ReactMarkdown from "react-markdown";

import { buildMenuTree } from "../../utils/function.js";

import "./index.scss";
import { useState } from "react";

function getMdsMenus() {
  return buildMenuTree(
    require.context("../../../public/mds", true, /\.md$/).keys()
  );
}

export default function MdsView() {
  let [mdfileContent, setMdfileContent] = useState("");
  // 
  const changeCurrentArticle = async (url) => {
    const res = await fetch(url);
    const content = await res.text();
    setMdfileContent(content);
  };

  let menus = getMdsMenus();

  function handleMenusItem(itemInfo) {
    changeCurrentArticle('/mds' + itemInfo.key.split("-key-")[1]);
  }

  return (
    <div className="mds-view-wrap">
      <div className="mds-menus">
        <div className="mds-view-logo">fz-mds-viewer</div>
        <Menu
          style={{ width: 256 }}
          mode="inline"
          items={menus}
          onClick={handleMenusItem}
        />
      </div>
      <div className="mds-viewer">
        <div className="md-title">fileName</div>
        <ReactMarkdown>{mdfileContent}</ReactMarkdown>
      </div>
    </div>
  );
}
